body {
  display: flex;
  flex-direction: column;
  font-family: Montserrat, sans-serif;
  padding: 1rem;
  min-height: 100vh;
}

#root {
  display: flex;
  flex-direction: column;
  flex: 2;
  background-color: #e5f6e5;
  background-color: var(--backgroundColor);
  min-height: calc(100vh - 2rem);
}

.wrapper {
  display: flex;
  flex-direction: column;
  flex: 2;
}

.headerAndMainHolder {
  display: flex;
  flex-direction: column;
  flex: 2;
  min-height: calc(100vh - 2rem);
}

main {
  display: flex;
  flex-direction: column;
  flex: 2;
}

h1 {
  display: none;
}

h2 {
  font-size: 2.5rem;
  font-size: 2.5rem;
  font-size: var(--titlesize);
  text-align: center;
}

h2,
h3,
h4 {
  font-weight: bold;
}

button {
  border: none;
  background: none;
  outline: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  cursor: pointer;
}

input,
textarea {
  padding: 1rem;
  border-radius: .5rem;
  border-radius: var(--borderRadius);
  font-family: Montserrat, sans-serif;
  border: none;
  font-size: 2rem;
  font-weight: lighter;
  width: 100%;
}

input:focus, textarea:focus {
    outline: none;
  }

textarea {
  resize: none;
}

label {
  display: block;
}

label span {
  display: block;
  font-size: 2rem;
  color: white;
  color: var(--secondColorBgTextColor);
  margin-bottom: 0.5rem;
}

.labelWithCheckbox {
  position: relative;
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  align-items: center;
  font-weight: lighter;
  cursor: pointer;
}

.labelWithCheckbox span {
    display: block;
    margin-bottom: 0;
    font-size: 1.6rem;
    font-size: var(--broodtekst);
  }

.labelWithCheckbox input[type="checkbox"] {
    position: absolute;
    top: 0;
    left: 0;
    width: auto;
    opacity: 0;
    cursor: pointer;
  }

.labelWithCheckbox input[type="text"] {
    order: -1;
    margin-left: 1rem;
  }

.labelWithCheckbox input[type="text"]:disabled {
    opacity: 0.5;
  }

.labelWithCheckbox input[type="checkbox"]:checked ~ .customCheckBox {
    background-color: #ee4266;
    background-color: var(--activeColor);
    background-image: url(/assets/img/checked.svg);
    background-repeat: no-repeat;
    background-position: center center;
  }

.customCheckBox {
  display: block;
  background-color: white;
  min-width: 2.4rem;
  max-width: 2.4rem;
  height: 2.4rem;
  border-radius: 0.2rem;
  margin: 0;
  margin-right: 1rem;
  cursor: pointer;
}

nav {
  margin: 0 0 2rem;
}

nav ul {
    display: flex;
  }

nav ul li {
    flex: 2;
    text-align: center;
    font-size: 1.6rem;
  }

nav ul li a,
  nav ul li button {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
    color: black;
    color: var(--defaultFontColor);
    font-weight: lighter;
    font-family: Montserrat, sans-serif;
    font-size: 1.6rem;
    font-size: var(--broodtekst);
  }

nav ul li a::before,
  nav ul li button::before {
    content: "";
    display: block;
    width: 3.5rem;
    height: 3.5rem;
    margin: 1rem;
    background-repeat: no-repeat;
  }

.ontdek_icon::before {
  background-image: url(/assets/img/ontdek_icon.svg);
}

.ontdek_icon_active::before {
  background-image: url(/assets/img/ontdek_icon_active.svg);
}

.login_icon::before {
  background-image: url(/assets/img/login_icon.svg);
}

.login_icon_active::before {
  background-image: url(/assets/img/login_icon_active.svg);
}

.projecten_icon::before {
  background-image: url(/assets/img/projecten_icon.svg);
  background-size: contain;
  background-position: center center;
}

.projecten_icon_active::before {
  background-image: url(/assets/img/projecten_icon_active.svg);
  background-size: contain;
  background-position: center center;
}

.upload_icon::before {
  background-image: url(/assets/img/upload_icon.svg);
}

.upload_icon_active::before {
  background-image: url(/assets/img/upload_icon_active.svg);
}

.logout_icon::before {
  background-image: url(/assets/img/logout.svg);
}

.activeLink a {
  color: #ee4266;
  color: var(--activeColor);
  font-weight: bold;
}

footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #4ecdc4;
  background-color: var(--secondColorBg);
  padding: 2rem;
  position: relative;
  z-index: 101;
}

footer p {
    width: 19rem;
    height: 6.6rem;
    background-image: url(/assets/img/howest.svg);
  }

footer .socialLinks {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
  }

footer .socialLinks li {
      margin-left: 1rem;
    }

footer .socialLinks li:first-of-type {
      margin-left: 0;
    }

footer .socialLinks li a {
      display: block;
      width: 3.5rem;
      height: 3.5rem;
      overflow: hidden;
      color: transparent;
    }

.facebook {
  background-image: url(/assets/img/fb.svg);
}

.insta {
  background-image: url(/assets/img/insta.svg);
}

.twitter {
  background-image: url(/assets/img/twitter.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
}

.sm_mail {
  background-image: url(/assets/img/mail.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
}

.lastSection {
  padding-bottom: 2rem;
}

.colorBg {
  position: relative;
  flex: 2;
  background-color: #4ecdc4;
  background-color: var(--secondColorBg);
  padding: 0 1rem;
  margin-top: 4.9rem;
}

.colorBgTop::before {
  content: "";
  display: block;
  position: absolute;
  top: -6.9rem;
  left: 0;
  width: 100%;
  height: 7rem;
  background-image: url(/assets/img/tileableWave.svg);
  background-size: auto 400%;
  background-repeat: repeat-x;
  background-position: top;
}

.btn,
.ghostBtn {
  width: 100%;
  display: block;
  padding: 1rem;
  color: white;
  color: var(--secondColorBgTextColor);
  text-transform: uppercase;
  font-weight: 700;
  font-weight: var(--weight-bold);
  font-size: 2.5rem;
  font-size: var(--titlesize);
  background-color: #ee4266;
  background-color: var(--activeColor);
  border-radius: .5rem;
  border-radius: var(--borderRadius);
  text-decoration: none;
  text-align: center;
}

.ghostBtn {
  color: #ee4266;
  color: var(--activeColor);
  background: none;
  border: solid 0.3rem #ee4266;
  border: solid 0.3rem var(--activeColor);
}

progress {
  position: relative;
  z-index: 60;
  display: block;
  height: 0.4rem;
  width: 100%;
  background-color: #e5f6e5;
  background-color: var(--backgroundColor);
}

progress::-webkit-progress-value {
  background: #ffe66d;
  background: var(--videoPlayerColor);
}

progress::-moz-progress-bar {
  background: #ffe66d;
  background: var(--videoPlayerColor);
}

.videoPlayer {
  position: relative;
  overflow: hidden;
}

.videoPlayer .videoHolder {
    width: 100%;
  }

.videoPlayer .videoHolder video {
    width: 100%;
    border-radius: .5rem;
    border-radius: var(--borderRadius);
  }

.videoControls {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 1rem;
}

.videoControls .playBtn {
    background-image: url(/assets/img/play.svg);
    width: 2rem;
    height: 2rem;
    overflow: hidden;
    color: transparent;
    margin-right: 1rem;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
  }

.videoControls .pauseBtn {
    background-image: url(/assets/img/pause.svg);
  }

.videoControls .progressBarHolder {
    position: relative;
    align-self: flex-start;
    height: 0.2rem;
    flex: 2;
  }

.videoControls .fullScreenBtn {
    width: 2rem;
    height: 2rem;
    background-image: url(/assets/img/fullscreen.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
    margin-left: 1rem;
  }

.commentsHolder {
  position: absolute;
  z-index: 30;
  top: -2.9rem;
  left: 0;
  width: 100%;
  font-size: 1.4rem;
}

.commentsHolder .comment {
    position: absolute;
    z-index: 5;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    padding: 0.5rem;
    padding-left: 0;
    opacity: 0;
  }

.commentsHolder .comment p {
    padding-top: 0.3rem;
    margin-bottom: 0;
  }

.commentsHolder .commentShow {
    -webkit-animation: showComment 2.5s;
            animation: showComment 2.5s;
  }

.commentsHolder .comment::before {
    content: "";
    display: block;
    position: absolute;
    z-index: -1;
    top: 0.5rem;
    left: 0;
    width: 100%;
    height: calc(100% - 1rem);
    background-color: #e5f6e5;
    background-color: var(--backgroundColor);
    opacity: 0.6;
    border-top-right-radius: 1.2rem;
    border-top-left-radius: 1.2rem;
  }

.commentsHolder .commentImg {
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
    width: 2.4rem;
    height: 2.4rem;
    overflow: hidden;
    background-color: #ee4266;
    background-color: var(--activeColor);
    margin-right: 0.5rem;
  }

.commentsHolder .commentImg img {
    width: 100%;
    height: 100%;
  }

.loading-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #4ecdc4;
  background-color: var(--secondColorBg);
  z-index: 1000;
}

.loading-wrapper h3 {
    font-size: 2.5rem;
    font-size: var(--titlesize);
    font-weight: 700;
    font-weight: var(--weight-bold);
  }

.loading-wrapper p {
    font-size: 1.6rem;
    font-size: var(--broodtekst);
    font-weight: 700;
    font-weight: var(--weight-bold);
    color: #e5f6e5;
    color: var(--backgroundColor);
    text-align: center;
  }

.loading-wrapper img {
    -webkit-animation: showPeter 5s infinite;
            animation: showPeter 5s infinite;
  }

@-webkit-keyframes showPeter {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(2);
            transform: scale(2);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes showPeter {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(2);
            transform: scale(2);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes showComment {
  0% {
    opacity: 0;
  }

  20% {
    opacity: 1;
  }

  80% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes showComment {
  0% {
    opacity: 0;
  }

  20% {
    opacity: 1;
  }

  80% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.miniComment {
  position: absolute;
  z-index: 40;
  top: -3.8rem;
  left: 0;
  display: flex;
  align-items: center;
  width: 100%;

  transition: all 0.3s;
}

.miniComment::before {
    content: "";
    display: block;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-top-left-radius: 1.9rem;
    border-top-right-radius: 1.9rem;
    background-color: #e5f6e5;
    background-color: var(--backgroundColor);
    opacity: 0.6;
  }

.miniComment .commentImg {
    display: block;
    height: 3.8rem;
    min-width: 3.8rem;
    max-width: 3.8rem;
    background-color: #ee4266;
    background-color: var(--activeColor);
    overflow: hidden;
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
  }

.miniComment .commentImg img {
      display: block;
      width: 100%;
      height: 100%;
    }

.miniComment input[type="text"] {
    margin: 0 0.5rem;
    padding: 1rem 0;
    background-color: transparent;
    font-size: 1.4rem;
  }

.miniComment input[type="submit"] {
    padding: 0;
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    color: transparent;
    background-color: #ee4266;
    background-color: var(--activeColor);
    background-image: url(/assets/img/comment.svg);
    background-size: 2rem;
    background-repeat: no-repeat;
    background-position: center center;
    margin-right: 0.5rem;
    cursor: pointer;
  }

.hideComment {
  opacity: 0;
}

.videoPlayerInfo {
  margin: 0 1rem;
}

.videoPlayerInfo header {
    text-transform: uppercase;
    margin-bottom: 1rem;
  }

.videoPlayerInfo video {
    width: 100%;
  }

.videoPlayerInfo p {
    margin-top: 1rem;
    font-size: 1.6rem;
    font-size: var(--broodtekst);
    text-align: center;
    line-height: 2.2rem;
  }

.cardHolder {
  margin: 0 1rem;
}

.card {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  margin-bottom: 2rem;
}

.card article {
  flex: 2;
  display: flex;
  flex-direction: column;
  font-size: 1.4rem;

  color: black;

  color: var(--defaultFontColor);
  border-radius: .5rem;
  border-radius: var(--borderRadius);
  background-color: white;
  background-color: var(--cardColor);
}

.card article .cardVideoHolder {
    border-top-right-radius: .5rem;
    border-top-right-radius: var(--borderRadius);
    border-top-left-radius: .5rem;
    border-top-left-radius: var(--borderRadius);
    overflow: hidden;
    order: -1;
  }

.card article .cardVideoHolder video {
    max-width: 100%;
    max-height: 100%;
  }

.card article img {
    border-radius: 50%;
    display: block;
    width: 4rem;
    height: 4rem;
    min-width: 4rem;
    overflow: hidden;
  }

.card article header {
    display: flex;
    align-items: center;
    padding: 0.5rem;
  }

.card article header h3 {
      font-weight: normal;
      font-size: 2rem;
      order: 1;
      flex: 2;
      margin-left: 0.5rem;
    }

.card article .contributorsData {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.5rem;
    height: 5rem;
  }

.card article .contributorsData .contributors {
      display: flex;
      flex: 2;
    }

.fade-enter {
  position: absolute;
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 0;
  transition: opacity 300ms;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  transition: opacity 300ms;
}

.fade-exit-done {
  opacity: 0;
}

.projectStats {
  display: flex;
}

.projectStats li {
    margin-left: 0.5rem;
  }

.projectStats li::before {
    content: "";
    display: inline-block;
    width: 1.7rem;
    height: 1.5rem;
    margin-right: 0.2rem;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
  }

.likes::before {
  background-image: url(/assets/img/like.svg);
}

.numMakers::before {
  background-image: url(/assets/img/hamer.svg);
}

.numErgo::before {
  background-image: url(/assets/img/ergomuts.svg);
}

.sorter {
  display: flex;
  border-bottom: solid 0.5rem #b0c9b0;
  border-bottom: solid 0.5rem var(--backgroundColorDark);
  margin: 0 1rem 1rem;
}

.sorter li {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 2;
  }

.sorter li button {
    padding: 0.5rem;
    font-size: 1.4rem;
    font-weight: lighter;
  }

.sorter .activeSort {
    position: relative;
  }

.sorter .activeSort button {
    font-weight: bold;
    color: #ee4266;
    color: var(--activeColor);
  }

.sorter .activeSort::after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    bottom: -0.5rem;
    width: 100%;
    height: 0.5rem;
    background-color: #ee4266;
    background-color: var(--activeColor);
  }

.highLightedSection {
  position: relative;
  margin: 7rem 0;
  background-color: #4ecdc4;
  background-color: var(--secondColorBg);
}

.highLightedSection::before,
  .highLightedSection::after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    width: 100%;
    height: 5rem;
    background-image: url(/assets/img/tileableWave.svg);
    background-size: auto 400%;

    background-repeat: repeat-x;
  }

.highLightedSection::before {
    top: -4.9rem;
    background-position: center top;
  }

.highLightedSection::after {
    bottom: -4.9rem;
    background-position: center top;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }

.highLightedSection .mainHeader {
    color: white;
    color: var(--secondColorBgTextColor);

    margin-bottom: 2rem;
  }

/* HOME */

.filterAndHeader header {
  margin-bottom: 2rem;
}

.zoekFilterHolder {
  display: flex;
  margin: 0 1rem 2rem;
}

.zoekFilterHolder label {
    flex: 2;
  }

.zoekFilterHolder label input {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    height: 100%;
  }

.openFilterBtn {
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  align-items: center;
  background-color: #ee4266;
  background-color: var(--activeColor);
  border-top-right-radius: .5rem;
  border-top-right-radius: var(--borderRadius);
  border-bottom-right-radius: .5rem;
  border-bottom-right-radius: var(--borderRadius);
  font-family: Montserrat, sans-serif;
  color: white;
  color: var(--activeTextColor);
}

.openFilterBtn::before {
    content: "";
    display: block;
    height: 2rem;
    width: 2rem;
    background-image: url(/assets/img/filter.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
  }

/* Project detail */

.projectDetailInfo {
  padding: 0 1rem;
  margin: 2rem 0 10rem;
}

.projectDetailInfo p {
    font-size: 1.6rem;
    font-size: var(--broodtekst);
    line-height: 2.2rem;
    margin-bottom: 2rem;
  }

.projectDetailInfo .projectStats {
    justify-content: center;
    align-items: center;
    margin: 1rem 0;
  }

.projectDetailInfo .links a:first-of-type {
    margin-bottom: 1rem;
  }

.data_description {
  min-height: 8rem;
}

.headerWithLikes {
  display: flex;
  margin: 2rem 0 1rem;
}

.headerWithLikes .projectDetailTitle {
    margin-left: 0.5rem;
  }

.headerWithLikes header {
    display: flex;
    flex: 2;
  }

.headerWithLikes header h3 {
    font-weight: 400;
    font-size: 2rem;
