    width: 100%;
    height: 100%;
  }

.profilePicHolder input {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    font-size: 100vw;
    opacity: 0;
  }

.prevAndNextHolder {
  display: flex;
}

.prevAndNextHolder button:last-of-type {
    margin-left: 1rem;
  }

.stepCounter {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
}

.stepCounter li {
    position: relative;
    border-radius: 50%;
    margin-left: 1rem;
    width: 1rem;
    height: 1rem;
    background-color: #e5f6e5;
    background-color: var(--backgroundColor);
  }

.stepCounter li:first-of-type {
    margin-left: 0;
  }

.stepCounter .activeStep::before {
    content: "";
    display: block;
    position: absolute;
    top: -0.1rem;
    left: -0.1rem;
    width: 1.2rem;
    height: 1.2rem;
    background-color: #ee4266;
    background-color: var(--activeColor);
    border-radius: 50%;
  }

.leeftijdHolder {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto 1fr;
  grid-column-gap: 2rem;
}

.leeftijdHolder legend {
    grid-column: 1 / -1;
    grid-row: 1 / 2;
  }

.leeftijdHolder label {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    text-align: center;
  }

.leeftijdHolder label span {
    font-size: 1.6rem;
    font-size: var(--broodtekst);
  }

.leeftijdHolder .leeftijdCheckbox {
    width: 8rem;
    height: 8rem;
    background-color: #dad8df;
    border-radius: 50%;
    cursor: pointer;
    background-position: center center;
    background-repeat: no-repeat;
  }

.leeftijdHolder input[type="checkbox"] {
    opacity: 0;
  }

.leeftijdHolder input[type="checkbox"]:checked ~ .leeftijdCheckbox {
    background-color: #ee4266;
    background-color: var(--activeColor);
  }

.kinderenType {
  background-image: url(/assets/img/kind.svg);
}

.jongerenType {
  background-image: url(/assets/img/jongere.svg);
}

.volwassenenType {
  background-image: url(/assets/img/volwassene.svg);
}

.ouderenType {
  background-image: url(/assets/img/oudere.svg);
}

/* Mijn projecten */

.mijnProjectenSection header {
  margin-bottom: 2rem;
}

.delen_icon,
.contacteren_icon {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.delen_icon::before {
  content: "";
  display: block;
  width: 1.6rem;
  height: 2rem;
  background-image: url(/assets/img/delen.svg);
  margin-right: 1rem;
}

.contacteren_icon::before {
  content: "";
  width: 2rem;
  height: 2.3rem;
  background-image: url(/assets/img/contacteren.svg);
  margin-right: 1rem;
  background-size: contain;
  background-repeat: no-repeat;
}

.diploma-uitleg {
  text-align: center;
  color: white;
  color: var(--cardColor);
  font-size: 1.6rem;
  font-size: var(--broodtekst);
  width: 29rem;
  line-height: 2rem;
  margin-top: 3rem;
}

.upload-diploma {
  display: flex;
  flex-direction: column;
}

/* TABLET */

@media screen and (min-width: 48em) {
  /* NAV */

  .topHeader {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2rem 3rem;
  }

  nav {
    margin: 0;
  }

    nav ul li {
      margin-left: 4rem;
    }

  h1 {
    display: block;
    width: 11rem;
    height: 3.6rem;
  }

    h1 span {
      display: none;
    }

  .logo {
    background-image: url(/assets/img/logo.svg);
  }

  .videoPlayerInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 5rem 0;
  }

    .videoPlayerInfo header {
      margin-bottom: 2rem;
    }

    .videoPlayerInfo .videoPlayer {
      max-width: 46rem;
    }

    .videoPlayerInfo p {
      margin-top: 2rem;
