      background-repeat: no-repeat;
      background-position: center center;
      overflow: hidden;
      color: transparent;
    }

    .cardHolderBtns .sliderBtn:disabled {
      cursor: default;
      opacity: 0;
    }

    .cardHolderBtns .sliderBtn.hide {
      display: none;
    }

