  }

  .filter {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .zoekFilterHolder {
    width: 36rem;
  }

  .sorter {
    margin: 0 3rem 2rem;
  }

    .sorter li button {
      font-size: 2.5rem;
      font-size: var(--titlesize);
    }

  /* LOGIN */

  .loginHolder .colorBg {
    flex-direction: row;
    justify-content: space-between;
    padding: 0 3rem;
  }

    .loginHolder .colorBg .loginFormHolder {
      display: flex;
      align-items: center;
      max-width: 32rem;
    }

      .loginHolder .colorBg .loginFormHolder form {
        margin-top: -5vh;
        flex: 2;
      }

      .loginHolder .colorBg .loginFormHolder button {
        margin-top: 3rem;
      }

    .loginHolder .colorBg .registerHolder {
      margin-top: -5vh;
      margin-bottom: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

      .loginHolder .colorBg .registerHolder header {
        display: flex;
        flex-direction: column;
        align-items: center;
      }

      .loginHolder .colorBg .registerHolder header::after {
        content: "";
        display: block;
        width: 13rem;
        height: 18rem;
        background-image: url(/assets/img/accountloos.svg);
        background-repeat: no-repeat;
        background-position: center center;
        margin: 2rem 0;
      }

      .loginHolder .colorBg .registerHolder a {
        width: 32rem;
      }

  /* REGISTRATION */
    .mainRegisterHolder legend {
      margin-top: 0;
    }

  .mainRegisterHolder .formContentHolder {
    width: 32rem;
    margin: 0 auto;
  }

  .prevAndNextHolder {
    justify-content: space-between;
    margin: 0 auto;
    width: 52rem;
  }

    .prevAndNextHolder button {
      width: auto;
      margin: 0;
    }
    .userSelector .formContentHolder {
      width: auto;
    }

    .userSelector legend {
      margin: 0;
    }

    .userSelector .typeHolder {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
    }

    .userSelector .typeHolder label {
      margin-left: 8rem;
    }

    .userSelector .typeHolder label:first-of-type {
      margin-left: 0;
    }

    .userSelector .typeHolder .selectedType {
      width: 8.8rem;
      height: 8.8rem;
    }

    .userSelector .btn {
      margin: 0 auto;
      width: 32rem;
    }

  .mainRegisterHolder .leeftijdHolder {
    width: 52rem;
  }

  .mainRegisterHolder .withAge {
    width: auto;
  }

  .leeftijdHolder {
