    .leeftijdHolder input {
      display: none;
    }

    .leeftijdHolder .leeftijdCheckbox {
      width: 8rem;
      height: 8rem;
    }

  /* PROJECT DETAIL */

  .projectDetailInfo {
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    grid-template-rows: repeat(4, auto);
    grid-column-gap: 2rem;
  }

    .projectDetailInfo .headerWithLikes {
      grid-column: 3 / 4;
      grid-row: 1 / 2;
    }

    .projectDetailInfo .playerHolder {
      display: flex;
      justify-content: center;
      align-items: center;
      grid-column: 1 / 2;
      grid-row: 1 / -1;
    }

    .projectDetailInfo .playerHolder .videoPlayer {
      width: 100%;
    }

    .projectDetailInfo .bodyPartViewer {
      margin: 0;
      align-self: center;
      grid-column: 2 / 3;
      grid-row: 1 / -1;
    }

    .projectDetailInfo .projectStats {
      grid-column: 3 / 4;
      grid-row: 2 / 3;
      justify-content: flex-start;
