  }

  .makerInfo img {
    min-width: 4.5rem;
    min-height: 4.5rem;
    max-width: 4.5rem;
    max-height: 4.5rem;
  }

  .instructieBtn {
    margin-left: auto;
    margin-right: auto;
    width: 31rem;
  }
}

/* DESKTOP */

@media screen and (min-width: 62em) {
  h2 {
    font-size: 3.5rem;
  }

  /* HOME */

  .videoPlayerInfo {
    position: relative;
  }

    .videoPlayerInfo header {
      margin-bottom: 3rem;
    }

    .videoPlayerInfo h2 {
      font-size: 4rem;
